.add_product {
  margin: auto;
  padding: 0rem;
  width: 34rem;

  label {
    font-weight: bold;
  }
}

.p_name {
  width: 20rem;
  max-width: 20rem;
}

.background {
  width: 100%;
  position: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  height: 340px;
  background-position: 50% 0%;
}

.search_box {
  width: 50rem;
  padding: 6rem 0;
  margin: auto;

  input {
    padding: 2rem;
    border-radius: 3rem;
  }
}
